<template>
    <div class="custom-checkbox">
        <label>
            <input v-model="localChecked" @change="emitChange" type="checkbox">
            <span :class="{ active: localChecked }">{{ label }}</span>
        </label>
    </div>
</template>

<script>
export default {
    props: ['is_checked', 'label'],
    data() {
        return {
            localChecked: this.is_checked
        }
    },
    watch: {
        is_checked(newVal) {
            this.localChecked = newVal;
        }
    },
    methods: {
        emitChange() {
            this.$emit('CheckChange', this.localChecked);
        }
    }
}
</script>
<style lang="scss">
.custom-checkbox {
    label {
        display: flex;
        align-items: center;
    }
    input[type=checkbox] {
        appearance: none;
        margin: 0;
        font: inherit;
        width: 1.5em;
        height: 1.5em;
        border: 0.1em solid #464646;
        border-radius: 7px;
        transform: translateY(-0.075em);
        margin: 5px 0;
    }
    input[type="checkbox"]:checked {
        background-color: #1867c0;
        background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 10.2L3.5 7.7L2.5 8.7L6 12.2L14 4.2L13 3.2L6 10.2Z'/%3E%3C/svg%3E");
        background-size: 100% 100%;
        background-position: center;
        background-repeat: no-repeat;
    }
    span {
        display: block;
        margin-left: 10px;
        font-family: Outfit;
        font-size: 16px;
        font-weight: 400;
        color: #757575
    }
}
</style>
