<template>
    <v-select
        v-model="model"
        :items="custom_list"
        label="Select a Brand"
        variant="outlined"
        class="mt-5">
    </v-select>
</template>

<script>
import { mapState } from "vuex";

export default {
    props: ['form'],
    data: () => ({
        model: null,
    }),
    computed: {
        ...mapState(['manufacturers']),
        custom_list() {
            const list = [];
            this.manufacturers.forEach(manufacturer => {
                list.push({
                    value: +manufacturer.id,
                    title: manufacturer.name
                });
            });
            return list;
        }
    },
    mounted() {
        this.model = +this.form.manufacturer_id;
    },
    watch: {
        model() {
            this.$emit('select', this.model);
        },
        'form.manufacturer_id': {
            handler() {
                this.model = this.form.manufacturer_id;
            },
            deep: true
        }
    }
}
</script>
