<template>
    <v-app id="catalog-app" class="outfit-font">
        <AppLayout>
            <template v-slot:content>
                <v-card-text>
                    <!--Featured Cards-->
                    <v-row v-if="FilteredProducts.length && ConfigShowFeaturedCards">
                        <v-col cols="12" lg="3" md="3" sm="6">
                            <FeaturedCard
                                color="#efd7d6"
                                btn-txt="Shop Now"
                                @click="ShowPrinterOffers"
                                title="Deal of the week"
                                body="Save up to 20% on desktop printers"
                                img-url="/wp-content/plugins/1-800-products/assets/img/sample-printer-1.png">
                            </FeaturedCard>
                        </v-col>
                        <v-col cols="12" lg="3" md="3" sm="6">
                            <FeaturedCard
                                color="#daede4"
                                btn-txt="View Deals"
                                @click="ShowFastePrinterOffers"
                                title="Exclusive Offers"
                                body="Save on selected models"
                                img-url="/wp-content/plugins/1-800-products/assets/img/sample-printer-2.png">
                            </FeaturedCard>
                        </v-col>
                        <v-col cols="12" lg="3" md="3" sm="6">
                            <FeaturedCard
                                color="#5c8eff6e"
                                btn-txt="Shop now"
                                @click="OpenShop"
                                title="Need Printer Supplies?"
                                body="Check out our store for your needs"
                                img-url="/wp-content/plugins/1-800-products/assets/img/sample-image-3.png">
                            </FeaturedCard>
                        </v-col>
                        <v-col cols="12" lg="3" md="3" sm="6">
                            <FeaturedCard
                                color="#f3e6d6"
                                btn-txt="Start"
                                @action="OpenHelpWindow"
                                :title="ConfigChatEnabled?'Not sure what to choose?':'Not sure what to do?'"
                                :body="ConfigChatEnabled?'Let our AI assistant help!':'Take a website tour'"
                                img-url="/wp-content/plugins/1-800-products/assets/img/sample-image-4.png">
                            </FeaturedCard>
                        </v-col>
                    </v-row>

                    <!--Sorter and Pagination-->
                    <Flexed justify="space-between" class="mt-3 mb-3">
                        <div style="color:rgba(0,0,0,0.6)" class="d-none d-md-block">{{ ActiveItemsShown }}</div>
                        <v-spacer/>
                        <v-select class="page-sorting" :model-value="filters.sort_type" @update:model-value="SetSortFilter" density="compact" variant="plain" :items="sort_types"/>
                    </Flexed>

                    <!--Product Card For Loop-->
                    <v-row id="the-products" v-if="!is_fetching">
                        <v-col :id="p_index==0?'first-product':''" v-for="(product,p_index) in PaginatedProducts" cols="12" lg="3" md="3" sm="6" xs="12">
                            <ProductCard :product="product"/>
                        </v-col>
                    </v-row>

                    <v-divider style="margin-top:30px;margin-bottom:20px"/>

                    <!--Pagination-->
                    <v-pagination
                        v-if="!is_fetching"
                        class="catalog-pagination"
                        :active-color="ConfigElementsColor"
                        v-model="active_page"
                        density="comfortable"
                        rounded="lg"
                        color="#00000080"
                        :length="TotalPages">
                    </v-pagination>
                </v-card-text>

                <IsFetchingData/>
                <NoCopiersAvail :data="FilteredProducts"/>
                <Configurator v-if="selected_copier && configurator"/>
                <ViewProduct/>
                <AiAssistantUI/>
                <ReviewProductForm v-if="review_modal"/>
            </template>
            <template #footer>
                <ShopLink/>
            </template>
        </AppLayout>

        <Alerts/>
        <CartTable/>
        <QuoteForm v-if="form_modal && cart_items.length > 0"/>
    </v-app>
</template>

<script>
import {mapActions, mapState} from "vuex";
import { useShepherd } from 'vue-shepherd';
import Modal from "@/components/misc/Modal.vue";
import AppLayout from "@/layouts/AppLayout.vue";
import Alerts from "@/components/misc/Alerts.vue";
import QuoteForm from "@/components/catalog/QuoteForm.vue";
import CartTable from "@/components/catalog/CartTable.vue";
import ProductCard from "@/components/catalog/ProductCard.vue";
import CheckoutBtn from "@/components/catalog/CheckoutBtn.vue";
import Configurator from "@/components/catalog/Configurator.vue";
import IsFetchingData from "@/components/catalog/IsFetchingData.vue";
import NoCopiersAvail from "@/components/catalog/NoCopiersAvail.vue";
import ViewProduct from "@/components/catalog/ViewProduct.vue";
import ActiveForm from "@/components/catalog/ActiveForm.vue";
import ShopLink from "@/components/misc/ShopLink.vue";
import Flexed from "./components/typography/Flexed.vue";
import FilterItem from "./components/catalog/FilterItem.vue";
import FeaturedCard from "./components/catalog/FeaturedCard.vue";
import AiAssistantUI from "@/components/assistant/AiAssistantUI.vue";
import ReviewProductForm from "@/components/configurator/ReviewProductForm.vue";
export default {
    components: {
        ReviewProductForm,
        Modal,
        Flexed,
        Alerts,
        ShopLink,
        QuoteForm,
        CartTable,
        FilterItem,
        AppLayout,
        ActiveForm,
        ProductCard,
        CheckoutBtn,
        ViewProduct,
        Configurator,
        NoCopiersAvail,
        IsFetchingData,
        FeaturedCard,
        AiAssistantUI,
    },
    data: () => ({
        active_page: 1,
        active_items: "",
    }),
    created() {
        this.FetchCatalogData();

        // Set website tour settings
        setTimeout(() => {
            this.SetAnyState({
                target: 'tour_instance',
                value: useShepherd({
                    cancelIcon: { enabled: true },
                    classes: 'class-1 class-2',
                    scrollTo: { behavior: 'smooth', block: 'center' },
                    useModalOverlay: true
                }),
            });
            this.$store.commit('SET_SHEPHERED_CANCELATION');
            this.tour_instance.addSteps(this.tour_steps);
            if (!this.selected_copier) {
                if (this.tour_enabled && !this.is_mobile) {
                    this.tour_instance.start();
                }
            }
        },500);
    },
    mounted() {
        // Set the AI Assistant config
        this.SetAiAssistantConfig();

        // Detect if device is mobile
        window.onload = () => {
            this.DetectScreenSize();
            window.addEventListener('resize', () => {
                this.DetectScreenSize();
            });

            // Start emitting to Analytics
            if (typeof gtag === 'function') {
                gtag('event', 'page_view', {
                    page_path: '/products',
                    page_title: 'Catalog Page'
                });
                console.log("Catalog now emits page visits to Analytics.")
            }
        }
    },
    methods: {
        ...mapActions([
            'SetError',
            'SetSuccess',
            'SetFilters',
            'SetAnyState',
            'SetCartItems',
            'FetchCatalogData',
            'SetSelectedCopier',
        ]),
        DetectScreenSize() {
            this.SetAnyState({
                target: 'is_mobile',
                value: window.innerWidth <= 768,
            });
        },
        AddToCartFromURL() {
            const urlParams = new URLSearchParams(window.location.search);
            if (urlParams.has('add-to-cart')) {
                const product_id = urlParams.get('add-to-cart');
                const product = this.products.find(p => p.id == product_id);

                console.log("product: ", product)
                if (typeof product != 'undefined') {
                    const current_items = [...this.cart_items];
                    current_items.push({product:product,datasets:[],quantity:1});
                    this.SetCartItems(current_items);
                    this.SetSuccess("1 item(s) added to quote");

                    // We decide what type of form to show based on product
                    if (product.manufacturer_id == 9) {
                        this.SetAnyState({target:'active_form',value:'mailing-form'});
                    }
                    else if (product.standard_function.toLowerCase() == 'wide-format') {
                        this.SetAnyState({target:'active_form',value:'wide-format-form'});
                    }
                    else if (product.standard_function.toLowerCase() == 'phone') {
                        this.SetAnyState({target:'active_form',value:'phone-form'});
                    }
                    else {
                        this.SetAnyState({target:'active_form',value:'default-form'});
                    }
                } else {
                    this.SetError("Sorry, this item is longer available.");
                }
            }
        },
        ConfigureFromURL() {
            const urlParams = new URLSearchParams(window.location.search);
            if (urlParams.has('configure')) {
                const product_id = urlParams.get('configure');
                const product = this.products.find(p => p.id == product_id);
                if (typeof product != 'undefined') {
                    this.SetSelectedCopier(product);
                    this.SetAnyState({
                        target: 'configurator',
                        value: true
                    });
                } else {
                    this.SetSelectedCopier(null);
                    this.SetAnyState({
                        target: 'configurator',
                        value: false
                    });
                    this.SetError("Sorry, this item is longer available.");
                }
            }
        },
        SetBrandFromURL() {
            const urlParams = new URLSearchParams(window.location.search);
            if (urlParams.has('brand')) {
                const brand_id = urlParams.get('brand');
                const brand = this.manufacturers.find(m => +m.id == +brand_id);
                if (typeof brand != 'undefined') {
                    const filters = {...this.filters}
                    if (!filters.manufacturers.includes(brand_id)) {
                        filters.manufacturers.push(+brand_id);
                    }
                }
            }
        },
        SetTypesFromURL() {
            const urlParams = new URLSearchParams(window.location.search);
            if (urlParams.has('type')) {
                const url_val = urlParams.get('type');
                const standard_func = this.standard_function.find(m => m.standard_function.toLowerCase() == url_val.toLowerCase());
                if (typeof standard_func != 'undefined') {
                    const filters = {...this.filters}
                    if (!filters.device_types.includes(url_val)) {
                        filters.device_types = [url_val];
                    }
                    this.SetAnyState({target: 'filters', value: filters});
                }
            }
        },
        SetSortFilter(value) {
            const rawFilters = {...this.filters};
            rawFilters.sort_type = value;
            this.SetFilters(rawFilters);
        },
        OpenShop() {
            window.open(this.ConfigShopURL, '_blank');
        },
        ScrollToProductCards() {
            if (this.is_mobile) {
                document.querySelector('#the-products').scrollIntoView({ behavior: 'smooth' });
            }
        },
        ShowPrinterOffers() {
            this.SetAnyState({target:'is_fetching',value:true});
            this.ScrollToProductCards();
            setTimeout(() => {
                const filtersCopy = {...this.filters};
                filtersCopy.price_filters = [
                    filtersCopy.price_options[0],
                    filtersCopy.price_options[1]
                ];
                filtersCopy.device_types  = ["Printer"];
                this.SetFilters(filtersCopy);
                this.SetAnyState({target:'is_fetching',value:false});
            },1000);
        },
        ShowFastePrinterOffers() {
            this.ScrollToProductCards();
            this.SetAnyState({target:'is_fetching',value:true});
            setTimeout(() => {
                const filtersCopy = {...this.filters};
                filtersCopy.price_filters = [];
                filtersCopy.sort_type = "price-low-high";
                filtersCopy.device_types = ["Copier","Wide-Format","MFP"];
                this.SetFilters(filtersCopy);
                this.SetAnyState({target:'is_fetching',value:false});
            },1000);
        }
    },
    computed: {
        ...mapState([
            'filters',
            'products',
            'cart_items',
            'is_fetching',
            'configurator',
            'manufacturers',
            'tour_steps',
            'tour_instance',
            'tour_enabled',
            'form_modal',
            'active_form',
            'selected_copier',
            'standard_function',
            'filters',
            'sort_types',
            'best_sellers',
            'is_mobile',
            'review_modal'
        ]),
        FilteredProducts() {
            let filteredProducts = [...this.products];
            this.active_page = 1;

            if (this.filters.manufacturers.length > 0) {
                filteredProducts = filteredProducts.filter(product =>
                    this.filters.manufacturers.includes(+product.manufacturer_id)
                );
            }
            if (this.filters.device_types.length > 0) {
                filteredProducts = filteredProducts.filter(product =>
                    this.filters.device_types.includes(product.standard_function)
                );
            }
            if (this.filters.copier_colors.length > 0) {
                filteredProducts = filteredProducts.filter(product =>
                    this.filters.copier_colors.includes(product.copier_color)
                );
            }
            if (this.filters.paper_sizes.length > 0) {
                filteredProducts = filteredProducts.filter(product =>
                    this.filters.paper_sizes.includes(product.copier_type)
                );
            }
            if (this.filters.speed_minimum >=0 && this.filters.speed_maximum > 0) {
                filteredProducts = filteredProducts.filter(product => {
                    return (
                        (product.speed_color < this.filters.speed_maximum && product.speed_color > this.filters.speed_minimum) ||
                        (product.speed_black < this.filters.speed_maximum && product.speed_black > this.filters.speed_minimum)
                    );
                });
            }
            if (this.filters.price_filters.length > 0) {
                filteredProducts = filteredProducts.filter(product => {
                    return this.filters.price_filters.some(range => {
                        return product.price >= range.min && product.price <= range.max;
                    });
                });
            }
            if (this.filters.sort_type) {
                if (this.filters.sort_type == 'ascending') {
                    filteredProducts.sort((a, b) => a.name.localeCompare(b.name));
                }
                else if (this.filters.sort_type == 'descending') {
                    filteredProducts.sort((a, b) => b.name.localeCompare(a.name));
                }

                /* Speed Sorter */
                else if (this.filters.sort_type == 'speed-low-high') {
                    filteredProducts.sort((a, b) => {
                        const speedA = a.speed_color > 0 ? a.speed_color : a.speed_black;
                        const speedB = b.speed_color > 0 ? b.speed_color : b.speed_black;
                        return speedA - speedB;
                    });
                }
                else if (this.filters.sort_type == 'speed-high-low') {
                    filteredProducts.sort((a, b) => {
                        const speedA = a.speed_color > 0 ? a.speed_color : a.speed_black;
                        const speedB = b.speed_color > 0 ? b.speed_color : b.speed_black;
                        return speedB - speedA;
                    });
                }

                /* Pricing Sorter */
                else if (this.filters.sort_type == 'price-low-high') {
                    filteredProducts.sort((a, b) => a.price - b.price);
                }
                else if (this.filters.sort_type == 'price-high-low') {
                    filteredProducts.sort((a, b) => b.price - a.price);
                }
            }

            // If searching, it resets all filters to maximize results
            if (this.filters.search_input.length > 0) {
                const searchTerm = this.filters.search_input.toLowerCase().trim();
                filteredProducts = this.products.filter(product => {
                    return (
                        product.name.toLowerCase().includes(searchTerm) ||
                        product.speed_black.toString().toLowerCase().includes(searchTerm) ||
                        product.speed_color.toString().toLowerCase().includes(searchTerm) ||
                        product.description.toLowerCase().includes(searchTerm) ||
                        product.model_number.toLowerCase().includes(searchTerm)
                    );
                });
            }

            return filteredProducts;
        },
        PaginatedProducts() {
            let start = (this.active_page - 1) * this.filters.page_limit;
            let end = start + this.filters.page_limit;
            return this.FilteredProducts.slice(start, end);
        },
        ActiveItemsShown() {
            if (!this.FilteredProducts.length) return "0 of 0 items";

            const start = (this.active_page - 1) * this.filters.page_limit + 1;
            let end = start + this.filters.page_limit - 1;
            end = Math.min(end, this.FilteredProducts.length);

            return `Showing ${start}-${end} of ${this.FilteredProducts.length} Products`;
        },
        TotalPages() {
            return Math.ceil(this.FilteredProducts.length / this.filters.page_limit);
        },
    },
    watch: {
        'products'() {
            this.AddToCartFromURL();
            this.ConfigureFromURL();
            this.SetBrandFromURL();
            this.SetTypesFromURL();
        }
    },
}
</script>
<style lang="scss">
.catalog-app {
    .pagination-wrap {
        max-width: 700px;
        margin: auto;
        margin-top: 30px;
    }
    .search-box.v-input--horizontal {
        margin: 0 !important;
        padding: 0 !important;
        min-height: 0;
        height: 40px;
    }
}
.page-sorting {
    position: relative;
    max-width: 218px;
    display: flex;
    padding-bottom: 5px;
    align-items: center;
    .v-select__selection-text {
        position:relative;
        font-weight: 500;
        color: #000;
    }
}
.catalog-pagination {
    max-width:500px;
    display: block;
    margin: auto;

    .v-pagination__item--is-active button {
        background-color: #1664C0 !important;
        color: #ffffff !important;
    }
}
</style>
