<template>
    <v-select
        variant="outlined"
        label="Copier color"
        :model-value="form.copier_type"
        @update:model-value="$emit('select',$event)"
        :items="copier_types"
        item-value="value"
        item-title="title"
        class="mt-5">
    </v-select>
</template>

<script>
import {mapState} from "vuex";

export default {
    props: ['form'],
    computed: {
        ...mapState(['copier_types'])
    },
}
</script>
