<template>
    <img width="35" class="mr-3" :src="AccessoryImage(accessory)" alt="Accessory Image">
    <Tooltip v-if="accessory.name.length>30" :text="accessory.name">
        <span>
            <span>{{ShortenName}}</span>
            <span v-if="accessory.selected" class="ml-1">(Selected)</span>
        </span>
    </Tooltip>
    <span v-else>
        <span>{{ShortenName}}</span>
        <span v-if="accessory.selected" class="ml-1">(Selected)</span>
    </span>
</template>

<script>
import {AccessoryMixins} from "@/mixins/AccessoryMixins";
import Tooltip from "@/components/typography/Tooltip.vue";

export default {
    components: {Tooltip},
    mixins: [AccessoryMixins],
    props: ['accessory'],
    computed: {
        ShortenName() {
            return this.accessory.name.length > 30 ? this.accessory.name.slice(0, 27) + '...' : this.accessory.name;
        }
    }
}
</script>
