            <p class="body">{{ body }}</p>
            <div class="footer">
                <div class="buttons">
                    <v-btn width="120" elevation="0" @click="$emit('click'),$emit('action')" :color="ConfigElementsColor" rounded="pill" class="mt-3">{{ btnTxt }}</v-btn>
                </div>
                <div class="image">
                    <img :src="imgUrl" alt="">
                </div>
            </div>
        </v-card-text>
    </v-card>
</template>

<script>
import {CommonMixins} from "@/mixins/CommonMixins";

export default {
    mixins: [CommonMixins],
    props: {
        title:   String,
        body:    String,
        btnTxt:  String,
        imgUrl:  String,
        color:   String
    }
};
</script>
