.featured-card {
    border-radius: 20px;
    .title {
        font-size:20px;
        font-family:Outfit;
        font-weight:500;
    }
    .body {
        font-weight:400;
        font-family: Inter, Outfit, Roboto, sans-serif;
        font-size: 14px;
    }
    .footer {
        display: flex;
        justify-content: space-between;
        .buttons {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        img {
            width: 100%;
            max-width:100px;
            max-height:100px;
        }
    }
