// Create a AccessoryMixins object to hold all of the methods for the Accessory component.

export const AccessoryMixins = {
    methods: {
        AccessoryImage(accessory) {
            const url = import.meta.env.VITE_SERVERAPI_URL + '/storage/uploads/accessories/featured';
            if (accessory.featured_image.length > 0) {
                return `${url}/${accessory.featured_image}`;
            } else {
                return `${url}/_placeholder_.png`;
            }
        },
        DatasetImage(dataset) {
            const url = import.meta.env.VITE_SERVERAPI_URL + '/storage/uploads/datasets';
            return `${url}/${dataset.filename}`;
        }
    }
}
