<template>
    <v-row justify="center" v-if="assistant.config">
        <v-dialog scrim="rgba(0,0,0,0.8)" v-model="assistant.visible" :fullscreen="false" persistent :max-width="assistant.config?.chatbox_width" transition="dialog-bottom-transition">
            <v-card :class="`${assistant.theme}-theme ${!assistant.visible?'hidden':''} animate-moveFromBottom`" :theme="assistant.theme" id="chat-ui">
                <v-card-title class="chat-header border-b d-flex">
                    <img v-if="assistant.config.icon_url" class="chat-icon" :src="assistant.config?.icon_url" alt="">
                    {{assistant.config?.title}}
                    <v-spacer/>
                    <Buttons/>
                </v-card-title>
                <v-card-text class="chat-body animate-fade-in">
                    <InitMessage v-if="!assistant.chats.length">
                        {{assistant.config.welcome_txt}} <br>
                        <small class="pr-5 pl-5 mb-5" style="width:100px">{{assistant.config.welcome_sub}}</small>
                        <ConvoStarters class="mt-5" v-if="!assistant.chats.length" @start="PredefinedConvo"/>
                    </InitMessage>
                    <Message v-for="chat in assistant.chats" :chat="chat"/>
                    <Message v-if="response.id" :chat="response"/>
                </v-card-text>
                <v-card-actions class="border-t">
                    <div class="chat-prompt-area">
                        <textarea ref="textarea" class="prompt-text" @keydown.enter.prevent="IdentifyIntent" v-model="user_prompt" @input="AdjustHeight" placeholder="Ask anything..."/>
                        <v-btn @click="IdentifyIntent" :loading="is_loading" :disabled="!user_prompt" variant="flat" size="small" class="prompt-btn" icon="mdi-play" color="info"/>
                    </div>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-row>
</template>

<script>
import axios from "axios"
import {mapState,mapActions} from "vuex";
import Message from "@/components/assistant/Message.vue";
import Buttons from "@/components/assistant/Buttons.vue";
import InitMessage from "@/components/assistant/InitMessage.vue";
import ConvoStarters from "@/components/assistant/ConvoStarters.vue";
export default {
    components: {ConvoStarters, InitMessage, Buttons, Message},
    data:() => ({
        is_loading: false,
        response: {
            id: null,
            role: "ai",
            message: "",
            timestamp: null,
        },
        user_prompt: "",
        user_intent: "no intent"
    }),
    computed: {
        ...mapState(['assistant','products']),
        History() {
            if (!this.assistant.config) return [];
            const {instructions,services} = this.assistant.config;
            const history = [];

            // Instructions
            history.push({role:"system",content:instructions});

            // Products,Supplies,Services list
            if (this.user_intent.includes('services')) {
                history.push({role:"user",content:(`Services List:\n${services}`)})
            }
            else if (this.user_intent.includes('printers')) {
                history.push({role:"user",content:(`Printers List:\n${this.CatalogProductsToString}`)})
            }
            else if (this.user_intent.includes('supplies')) {
                history.push({role:"user",content:(`
                    Supplies List:\n
                    - Brother | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=brother
                    - Canon | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=canon
                    - Copystar | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=copystar
                    - HP | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=hp
                    - Hyperion | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=hyperion
                    - Epson | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=epson
                    - Konica Minolta | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=konica-minolta
                    - Lexmark | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=lexmark
                    - Ricoh | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=Ricoh
                    - Samsung | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=samsung
                    - Sharp | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=sharp
                    - Xerox | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=xerox
                    - Toshiba | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=toshiba
                    - Others | https://shop.1800officesolutions.com/office-supplies/?pwb-brand-filter=others
                `)})
            }

            // Chat history with the customer
            this.assistant.chats.forEach(chat => {
                let role = chat.role=='ai'?'assistant':'user';
                history.push({role: role,content:chat.message});
            });

            history.push({role:"user",content:this.user_prompt});
            return history;
        },
        CatalogProductsToString() {
            let products_string = "";
            this.products.forEach(product => {
                products_string += (`-#${product.id} ${product.name}\n`)
            });
            return products_string;
        },
    },
    methods: {
        ...mapActions(['SetAnyState','SetError','AddChat','ToggleChatUI']),
        AdjustHeight() {
            this.$nextTick(() => {
                const textarea = this.$refs.textarea;
                if (textarea) {
                    textarea.style.height = "auto";
                    textarea.style.height = textarea.scrollHeight + "px";
                }
            });
        },
        SendPrompt() {
            // Insert the user prompt
            this.AddChat({
                id: Date.now(),
                role: "human",
                message: this.user_prompt,
                timestamp: new Date().toISOString()
            });
            this.user_prompt = "";
            this.is_loading = true;

            // Set initial AI response message
            this.response.id = Date.now();
            this.response.timestamp = new Date().toISOString();
            this.response.message = "Thinking...";

            const form = new FormData;
            form.append('history',JSON.stringify(this.History));

            axios.post("/wp-json/1-800-products/v1/send-prompt",form).then(res => {
                let words = res.data.choices[0].message.content.split(" ");
                let count = 0;
                this.response.message = "";
                let interval = setInterval(()=>{
                    if (count < words.length) {
                        this.response.message += words[count]+" ";count++;
                    } else {
                        clearInterval(interval);
                        this.AddChat({...this.response});
                        this.response = {
                            id: null,
                            role: "ai",
                            message: "",
                            timestamp: null,
                        }
                    }
                },50);
            }).catch(error => {
                console.log("Here 2: ", error)
                this.SetError("Oops! Something went wrong. Please try again in a moment.");
            }).finally(() => {
                this.is_loading = false;
            });
        },
        IdentifyIntent() {
            // This decides whether to include the products,supplies,services to context
            const system_prompt = (`
                Your task is to analyze the user input and chat history and determine if it expresses an intent related to any of the following categories:
                    1. Services – Inquiries about the services we provide.
                    2. Printers – Intent to lease or purchase a printer or VoIP phone.
                    3. Supplies – Intent to purchase office supplies such as toners, parts, or consumables.

                Respond with one of the following plain text labels based on the identified intent:
                    "services" – if the input relates to category 1
                    "printers" – if the input relates to category 2
                    "supplies" – if the input relates to category 3
                    "no intent" – if none of the above categories clearly apply
                Only return the plain text label.
            `);

            // Chat history with the customer
            const history = [{role: "system",content: system_prompt}];
            this.assistant.chats.forEach(chat => {
                let role = chat.role=='ai'?'assistant':'user';
                history.push({role:role,content:chat.message});
            });
            history.push({role:"user",content:`user input: ${this.user_prompt}`});

            const form = new FormData;
            form.append('history',JSON.stringify(history));

            this.is_loading = true;
            axios.post("/wp-json/1-800-products/v1/send-prompt",form).then(res => {
                this.user_intent = res.data.choices[0].message.content;
                this.SendPrompt();
            }).catch(error => {
                console.log("Here: ", error)
                this.SetError("Oops! Something went wrong. Please try again in a moment.");
            }).finally(() => {
                this.is_loading = false;
            });;
        },
        ScrollToBottom() {
            const chatBody = document.querySelector('.chat-body');
            const observer = new MutationObserver(() => {
                chatBody.scrollTop = chatBody.scrollHeight;
            });
            observer.observe(chatBody, { childList: true, subtree: true });
        },
        PredefinedConvo(text) {
            this.user_prompt = text;
            this.IdentifyIntent();
        },
        HandleEscape(event) {
            if (event.key === 'Escape') {
                this.ToggleChatUI();
            }
        }
    },
    mounted() {
        window.addEventListener('keydown', this.HandleEscape);
    },
    beforeUnmount() {
        window.removeEventListener('keydown', this.HandleEscape);
    },
    watch: {
        "config"(newVal) {
            if (newVal) {
                Axios.defaults.baseURL = this.$store.state.config.baseurl;
            }
        },
        "user_prompt"() {
            this.AdjustHeight();
        },
        "is_loading"(newVal) {
            if (newVal) {
                this.ScrollToBottom();
            }
        }
    },
}
</script>
<style scoped lang="scss">
#chat-ui {
    font-family: Outfit, Roboto, sans-serif;
    border-radius: 15px;
    .chat-icon {
        height: 30px;
        border: 1px solid rgba(255, 255, 255, 0.39);
        margin-right: 5px;
        border-radius: 50px;
        background-color: rgba(255, 255, 255, 0.39);
    }
    .init-message {
        text-align: center;
        font-weight: 200;
    }
    .chat-header {
        align-items: center;
    }
    .chat-body {
        padding: 15px;
        overflow: auto;
        max-height: 400px;
        background: #f1f1f1;
        min-height: 300px;
        position: relative;

        /* This is a css for AI generated suggestions */
        ul {
            margin-left: 15px;
        }
    }
    .chat-models {
        .v-input__details {
            display: none;
        }
    }
    .chat-prompt-area {
        background: #fff;
        border-radius: 10px;
        width: 100%;
        position: relative;
        display: flex;
        align-items: center;
        .prompt-text {
            width: 100%;
            max-height: 200px;
            min-height: 30px;
            padding: 10px !important;
            resize: none;
            overflow: auto;
            &:focus {
                border: none;
                outline: none;
            }
        }
        .prompt-btn {
            right: 5px;
        }
    }
}
.chat {
    border-radius: 20px;
    padding: 15px;
    margin: 10px;
    box-shadow: 0 5px 5px #eaeaea;
    .chat-date {
        font-size: 10px;
        opacity: .7;
        margin-bottom: 5px
    }
    &.from-ai {
        background: #ffffff;
        width: fit-content;
        max-width: 300px;
    }
    &.from-human {
        background: #0067ff;
        width: fit-content;
        max-width: 300px;
        color: #ffffff;
        margin-left: auto;
    }
}
#chat-ui.dark-theme {
    .chat-body {
        background: #212121;
        .init-message {
            color: #ffffff;
        }
    }
    .chat-body::-webkit-scrollbar {
        width: 8px;
    }
    .chat-body::-webkit-scrollbar-track {
        background: transparent;
    }
    .chat-body::-webkit-scrollbar-thumb {
        background-color: #888;
        border-radius: 4px;
        border: 2px solid transparent;
        background-clip: content-box;
    }
    .chat-prompt-area {
        background: #212121;
    }
    .chat {
        box-shadow: 0 5px 5px #1e1e1e;
        &.from-ai {
            background: #595959;
        }
    }
}
</style>
