<template>
    <v-expansion-panel>
        <v-expansion-panel-title :class="color">
            <v-icon :icon="icon"/>
            <span>&nbsp; {{title}}</span>

            <template v-if="$slots.buttons">
                <v-spacer v-if="$slots.buttons"/>
                <span @click.stop>
                    <slot name="buttons" />
                </span>
            </template>
        </v-expansion-panel-title>
        <v-expansion-panel-text style="padding: 15px 0;">
            <slot></slot>
        </v-expansion-panel-text>
    </v-expansion-panel>
</template>

<script>
export default {
    props: ['title','icon','color'],
}
</script>
