// Create a ProductMixin

export const ProductMixins = {
    methods: {
        BaseImage(product) {
            const url = import.meta.env.VITE_SERVERAPI_URL + '/storage/uploads/products';
            if (product?.base_image.length > 0) {
                return `${url}/base-img/${product.base_image}`;
            } else {
                return `${url}/base-img/_placeholder_.png`;
            }
        },
        ProductImage(product) {
            const url = import.meta.env.VITE_SERVERAPI_URL + '/storage/uploads/products';
            if (product?.product_image != null && product.product_image.length > 0) {
                return `${url}/product-img/${product.product_image}`;
            } else {
                return `${url}/product-img/_placeholder_.png`;
            }
        }
    }
}
