<template>
    <Modal max_width="700" title="Finalize your quote...[Active]" :visible="true" @close="CloseModal">
        <v-card-text>
            <v-row id="active-form">
                <!--Plotter/Wide-format Questionnaire-->
                <template v-if="active_form=='wide-format-form'">
                    <QuoteFormTitle>Plotter/Wide-format Printer Questionnaire</QuoteFormTitle>
                    <HowManyRolls/>
                    <PrintsPerMonth/>
                    <TypeOfPrinting/>
                    <PrintingDims/>
                    <NeedScanner/>
                </template>

                <!--Quadient Fields-->
                <template v-else-if="active_form=='mailing-form'">
                    <QuoteFormTitle>Mailing Equipment Questionnaire</QuoteFormTitle>
                    <LeaseTermSelector/>
                    <CopierQuantityMailing/>
                    <OwnsMailingMachine/>
                    <NumMailsPerMonth/>
                </template>

                <!--Phone Fields-->
                <template v-else-if="active_form=='phone-form'">
                    <QuoteFormTitle>Phone System Questionnaire</QuoteFormTitle>
                    <PhoneSysReason/>
                    <PhoneQuantity/>
                </template>

                <!--Default Copier Questionnaire-->
                <template v-else-if="active_form=='default-form'">
                    <QuoteFormTitle>Copier Questionnaire</QuoteFormTitle>
                    <LeaseTermSelector/>
                    <CopierQuantity/>
                    <AvgPrintSlider/>
                </template>
            </v-row>

            <v-card v-if="errors.length>0" elevation="0" border class="pl-5 mt-3" color="rgba(229,43,43,0.08)">
                <v-card-text>
                    <ul class="error-list">
                        <li class="text-red" v-for="error in errors">{{error}}</li>
                    </ul>
                </v-card-text>
            </v-card>
        </v-card-text>
        <template v-slot:footer>
            <v-btn class="d-none d-sm-flex" :disabled="is_loading || !Passable" @click="OpenCart" variant="tonal" prepend-icon="mdi-cart" color="primary">({{cart_items.length}}) items</v-btn>&nbsp; &nbsp;
            <v-btn class="d-none d-sm-flex" :disabled="is_loading || !Passable" @click="CloseModal" variant="tonal" prepend-icon="mdi-plus-circle-outline" color="primary">Add more...</v-btn>
            <v-spacer/>
            <v-btn class="align-sm-center" @click="GoToMainForm" append-icon="mdi-chevron-right" color="primary">Continue</v-btn>
        </template>
    </Modal>
</template>

<script>
import {mapActions, mapState} from "vuex";
import Modal from "@/components/misc/Modal.vue";
import {ProductMixins} from "@/mixins/ProductMixins";
import {AccessoryMixins} from "@/mixins/AccessoryMixins";
import StepTitle from "@/components/typography/StepTitle.vue";
import QuoteFormTitle from "@/components/catalog/QuoteFormTitle.vue";
import PhoneQuantity from "@/components/catalog/form/PhoneQuantity.vue";
import AvgPrintSlider from "@/components/catalog/form/AvgPrintSlider.vue";
import PhoneSysReason from "@/components/catalog/form/PhoneSysReason.vue";
import CopierQuantity from "@/components/catalog/form/CopierQuantity.vue";
import NumMailsPerMonth from "@/components/catalog/form/NumMailsPerMonth.vue";
import LeaseTermSelector from "@/components/catalog/form/LeaseTermSelector.vue";
import OwnsMailingMachine from "@/components/catalog/form/OwnsMailingMachine.vue";
import HowManyRolls from "@/components/catalog/form/HowManyRolls.vue";
import PrintsPerMonth from "@/components/catalog/form/PrintsPerMonth.vue";
import TypeOfPrinting from "@/components/catalog/form/TypeOfPrinting.vue";
import NeedScanner from "@/components/catalog/form/NeedScanner.vue";
import AddressFields from "@/components/catalog/form/AddressFields.vue";
import CompanyFields from "@/components/catalog/form/CompanyFields.vue";
import PrintingDims from "@/components/catalog/form/PrintingDims.vue";
import CopierQuantityMailing from "@/components/catalog/form/CopierQuantityMailing.vue";
import {CommonMixins} from "@/mixins/CommonMixins";

export default {
    components: {
        CopierQuantityMailing,
        PrintingDims,
        CompanyFields,
        AddressFields,
        NeedScanner,
        TypeOfPrinting,
        PrintsPerMonth,
        HowManyRolls,
        Modal,
        StepTitle,
        PhoneSysReason,
        CopierQuantity,
        PhoneQuantity,
        QuoteFormTitle,
        AvgPrintSlider,
        NumMailsPerMonth,
        LeaseTermSelector,
        OwnsMailingMachine
    },
    data:() => ({
        errors: [],
    }),
    mixins: [
        CommonMixins,
        ProductMixins,
        AccessoryMixins,
    ],
    computed: {
        ...mapState([
            'form_success',
            'is_loading',
            'form_modal',
            'states',
            'form',
            'cart_items',
            'accessories',
            'active_form',
            'copier_types',
        ]),
        copier_color_text() {
            let color_text = '';
            this.copier_types.forEach(item => {
                if (this.form.copier.copier_type == item.value) {
                    color_text = item.title
                }
            });
            return color_text;
        },
        Passable() {
            const form   = this.active_form;
            const copier = this.form.copier;
            const phone  = this.form.copier;
            if (form=='wide-format-form') {
                if (copier.how_many_rolls   &&
                    copier.need_a_scanner   &&
                    copier.type_of_printing &&
                    copier.prints_per_month &&
                    copier.printing_dims) {
                    return true;
                } else {
                    return false;
                }
            }
            if (form=='mailing-form') {
                if (copier.num_mails_per_month > 0 &&
                    copier.owns_mailing_machine &&
                    copier.copier_quantity > 0) {
                    return true;
                } else {
                    return false;
                }
            }
            if (form=='phone-form') {
                if (copier.phone_quantity>0 &&
                    copier.phone_sys_reason) {
                    return true;
                } else {
                    return false;
                }
            }
            if (form=='default-form') {
                if (copier.prints_per_month > 0 &&
                    copier.copier_quantity > 0) {
                    return true;
                } else {
                    return false;
                }
            }
            else {
                return false;
            }
        }
    },
    methods: {
        ...mapActions([
            'SetError',
            'SetSuccess',
            'SetSelectedCopier'
        ]),
        CloseModal() {
            if (!this.Passable) {
                return this.SetError('Please complete the form first.');
            }
            this.SetState('form_modal',false);
            this.SetState('cart_modal',false);
            this.SetState('configurator',false);
        },
        OpenCart() {
            this.CloseModal();
            this.SetState('cart_modal',true);
        },
        GoToMainForm() {
            if (this.Passable) {
                this.SetState('active_form',null);
                this.SetState('form_modal',true);
            } else {
                const form = this.active_form;
                this.errors = [];
                if (form == 'wide-format-form') {
                    if(!this.form.copier.how_many_rolls)   this.errors.push('How many rolls do you want your printer to have?');
                    if(!this.form.copier.prints_per_month) this.errors.push('How many prints do you print per month?');
                    if(this.form.copier.type_of_printing.length<1) this.errors.push('What type of printing do you do?');
                    if(!this.form.copier.printing_dims)    this.errors.push('Approximately how wide will the majority of your prints be?');
                    if(!this.form.copier.need_a_scanner)   this.errors.push('How handy would a scanner be for your business?');
                }

                /** Copiers Validation **/
                if (form == 'default-form') {
                    if (this.form.copier.copier_quantity <=0) this.errors.push('How many printers do you neeed?');
                    if (this.form.copier.prints_per_month<=0) this.errors.push('Approximate monthly print/copy volume');
                }

                /** Phones Validation */
                if (form == 'phone-form') {
                    if (!this.form.phone.phone_sys_reason) this.errors.push('Which describes your phone system needs?');
                    if (!this.form.phone.phone_quantity) this.errors.push('Approximately, how many phones do you need?');
                }

                /** Quadient Validation */
                if (form == 'mailing-form') {
                    if (this.form.copier.copier_quantity <=0) this.errors.push('How many printers do you neeed?');
                    if (this.form.copier.num_mails_per_month<=0) this.errors.push('How many pieces of mail are you sending per month?');
                    if (!this.form.copier.owns_mailing_machine) this.errors.push('Do you currently own a mailing machine?');
                }

                this.SetError('Please complete the form to proceed.');
            }
        }
    },
    mounted() {
        if (this.cart_items.length > 0) {
            this.form.copier.copier_type = this.cart_items[0].product.copier_type;
        }

        // Count how many business phones and set its value
        const form_copy  = {...this.form};
        const phone_qty  = this.cart_items.filter(c=>c.product.standard_function=='Phone').length;
        const copier_qty = this.cart_items.filter(c=>c.product.standard_function!='Phone').length;

        if (phone_qty > 0 && phone_qty <= 3) {
            form_copy.phone.phone_quantity = '1 To 3';
        }
        else if (phone_qty > 3 && phone_qty <= 49) {
            form_copy.phone.phone_quantity = '4 To 49';
        }
        else if (phone_qty > 49) {
            form_copy.phone.phone_quantity = '50+';
        }
        form_copy.copier.copier_quantity = copier_qty;
        this.SetState('form',form_copy);
    },
}
</script>
