<template>
    <!--Disabled until Elie provides the pricing-->
    <!--<p class="font-weight-bold">${{accessory.price}}</p>-->
    <Tooltip location="left" text="Unselect" v-if="dataset.selected">
        <v-btn :disabled="default_attached" id="small-button" variant="outlined" color="error" class="ml-5" icon="mdi-trash-can" size="x-small" @click="$emit('unselect')"/>
    </Tooltip>
    <Tooltip location="left" text="Add this accessory" v-else>
        <v-btn :color="ConfigElementsColor" class="ml-5" icon="mdi-plus" size="x-small" @click="$emit('select')"/>
    </Tooltip>
    <v-tooltip v-if="default_attached" target="small-button" activator="parent" location="right">This accessory is attached by default</v-tooltip>
</template>

<script>
import Tooltip from "@/components/typography/Tooltip.vue";
import {AccessoryMixins} from "@/mixins/AccessoryMixins";
import {CommonMixins} from "@/mixins/CommonMixins";

export default {
    components: {Tooltip},
    emits: ['unselect','select'],
    mixins: [AccessoryMixins,CommonMixins],
    props: ['accessory','dataset','datasets'],
    computed: {
        default_attached() {
            return this.dataset.default_attached==1;
        },
    }
}
</script>
