<template>
    <v-col cols="12" md="6" sm="12" xs="12">
        <v-autocomplete
            density="compact"
            v-model="form.copier.lease_term"
            :items="custom_list"
            label="Choose Lease Term"
            variant="outlined">
        </v-autocomplete>
    </v-col>
</template>

<script>
import {mapState} from "vuex";

export default {
    computed: {
        ...mapState(['lease_terms','form']),
        custom_list() {
            const list = [];
            this.lease_terms.forEach(lease_term => {
                list.push({
                    value: +lease_term.term,
                    title: lease_term.title
                });
            });
            return list;
        }
    },
}
</script>
