<template>
    <v-chip variant="flat" size="small" v-for="man_id in filters.manufacturers"  :color="ConfigElementsColor" class="mr-1 mb-2" @click="RemoveFilter('manufacturers',+man_id)">{{GetBrandName(man_id)}} &times;</v-chip>
    <v-chip variant="flat" size="small" v-for="device in filters.device_types"   :color="ConfigElementsColor" class="mr-1 mb-2" @click="RemoveFilter('device_types',device)">{{device}} &times;</v-chip>
    <v-chip variant="flat" size="small" v-for="colors in filters.copier_colors"  :color="ConfigElementsColor" class="mr-1 mb-2" @click="RemoveFilter('copier_colors',colors)">{{colors}} &times;</v-chip>
    <v-chip variant="flat" size="small" v-for="papersize in filters.paper_sizes" :color="ConfigElementsColor" class="mr-1 mb-2" @click="RemoveFilter('paper_sizes',papersize)">{{papersize}} &times;</v-chip>
    <v-chip variant="flat" size="small" v-for="price in filters.price_filters"   :color="ConfigElementsColor" class="mr-1 mb-2" @click="RemovePriceFilter(price)">{{price.label}} &times;</v-chip>
    <v-chip variant="flat" size="small" v-if="filters.sort_type" class="info mr-1 mb-2 text-capitalize" :color="ConfigElementsColor" @click="RemoveSorter">{{GetSortName(filters.sort_type)}} &times;</v-chip>
    <template v-if="filters.active_speed && filters.speed_maximum && filters.speed_minimum">
        <v-chip variant="flat" size="small" :color="ConfigElementsColor" class="mr-1 mb-2" @click="RemoveSpeedFilter">
            <span class="text-capitalize">{{filters.active_speed}}</span>&nbsp;Speed: {{filters.speed_minimum}}-{{filters.speed_maximum}} PPM  &times;
        </v-chip>
    </template>
</template>

<script>
import {mapActions, mapState} from "vuex";
import {CommonMixins} from "@/mixins/CommonMixins";

export default {
    mixins: [CommonMixins],
    computed: {
        ...mapState(['filters','manufacturers','sort_types'])
    },
    methods: {
        ...mapActions(['SetFilters']),
        RemoveFilter(target, value) {
            const filtersCopy = {...this.filters}
            let index = filtersCopy[target].indexOf(value);
            if (index !== -1) filtersCopy[target].splice(index,1);
            this.SetFilters(filtersCopy);
        },
        RemoveSorter() {
            const filtersCopy = {...this.filters}
            filtersCopy.sort_type = null;
            this.SetFilters(filtersCopy);
        },
        RemoveSpeedFilter() {
            const filtersCopy = {...this.filters}
            filtersCopy.active_speed  = null;
            filtersCopy.speed_minimum = null;
            filtersCopy.speed_maximum = null;
            this.SetFilters(filtersCopy);
        },
        GetBrandName(man_id) {
            const brand = this.manufacturers.find(man => man.id == man_id);
            return typeof brand == 'undefined' ? (''): brand.name
        },
        RemovePriceFilter(min_max) {
            const filtersCopy = {...this.filters}
            const index = filtersCopy.price_filters.findIndex(
                filter => filter.min === min_max.min && filter.max === min_max.max
            );
            if (index != -1) {
                filtersCopy.price_filters.splice(index,1);
            }
            this.SetFilters(filtersCopy);
        },
        GetSortName(value) {
            return this.sort_types.find(s => s.value == value).title;
        }
    }
}
</script>
