// store.js
import { createStore } from 'vuex';
import Server from "@/plugins/Server";
import createPersistedState from 'vuex-persistedstate';

export default createStore({
    state: {
        /** Catalog States */
        is_fetching: false,
        filters: {
            sort_type: null,
            page_limit: 12,
            search_input: '',
            manufacturers: [],
            device_types: [],
            copier_colors: [],
            paper_sizes: [],
            speed_minimum: null,
            speed_maximum: null,
            price_filters: [],
            price_options: [
                {
                    label: "$69-$99",
                    min: 69,
                    max: 99
                },
                {
                    label: "$100-$199",
                    min: 100,
                    max: 199
                },
                {
                    label: "$200+",
                    min: 200,
                    max: 9999
                }
            ],
        },
        snackbar: {
            visible: false,
            content: '',
            icon: '',
            type: 'success',
        },
        dialog: {
            visible: false,
            title: '',
            content: '',
        },
        sort_types: [
            {value: 'price-low-high', title:'Sort: Price (Low to High)'},
            {value: 'price-high-low', title:'Sort: Price (High to Low)'},
            {value: 'ascending',      title:'Sort: Name (A-Z)'},
            {value: 'descending',     title:'Sort: Name (Z-A)'},
            {value: 'speed-low-high', title:'Sort: Speed (Low to High)'},
            {value: 'speed-high-low', title:'Sort: Speed (High to Low)'},
            {value:  null,            title:'Sort: Default Order'}
        ],

        /** Quote Tool States */
        menu_items: [
            {
                title: 'Choose a copier type',
                value: 'client_dashboard',
                step: 1,
            },
            {
                title: 'Estimated monthly prints',
                value: 'client_contracts',
                step: 2,
            },
            {
                title: 'Configure your copier',
                value: 'client_agents',
                step: 3,
            },
            {
                title: 'Finalize your Quote',
                value: 'client_agents',
                step: 4,
            },
        ],
        copier_types: [
            {
                value: 'B&W',
                title: 'Black & White',
                icon: 'mdi-invert-colors-off',
            },
            {
                value: 'Color',
                title: 'Color',
                icon: 'mdi-invert-colors',
            },
            {
                value: null,
                title: 'Not Sure',
                icon: 'mdi-help-circle',
            },
        ],
        datasets: [],
        accessories: [],
        categories: [],
        lease_terms: [],
        product_pricing: [],
        cart_items: [],
        selected_copier: null,

        // Quote Form Component
        form: {
            address: {
                street: '',
                city: '',
                state: '',
                zip: '',
            },
            lead: {
                company: '',
                firstname: '',
                lastname: '',
                email: '',
                phone: '',
            },
            copier: {
                lease_term: 36,
                copier_type: null,
                copier_quantity: 0,
                prints_per_month: 0,

                // Mail machine
                num_mails_per_month: 0,
                owns_mailing_machine: null,

                // Plotter/wide-format
                how_many_rolls: null,
                need_a_scanner: null,
                type_of_printing: [],
                printing_dims: null,
            },
            phone: {
                phone_sys_reason: null,
                phone_quantity: null,
            },
        },

        // Shared States
        products: [],
        manufacturers: [],
        product_reviews: [],
        standard_function: [],

        // Printer IDs of best sellers
        // People have already quoted these printers
        best_sellers: [],

        // Configurator
        configurator: false,
        config_drawer: true,
        config_fullscreen: false,
        view_product: null,

        cart_modal: false,
        form_modal: false,
        form_success: false,
        review_modal: false,
        active_form: null,
        is_loading: false,
        addtocart_trigger: 0,
        states: [
            "AL", "AZ", "AR", "CA", "CO", "CT", "DE", "DC", "FL", "GA",
            "ID", "IL", "IN", "IA", "KS", "KY", "LA", "ME", "MD", "MA",
            "MI", "MN", "MS", "MO", "MT", "NE", "NV", "NH", "NJ", "NM",
            "NY", "NC", "ND", "OH", "OK", "OR", "PA", "RI", "SC", "SD",
            "TN", "TX", "UT", "VT", "VI", "VA", "WA", "WV", "WI", "WY",
        ],

        // Plugin Settings
        manufacturers_allowed: [],
        catalog_page_title: "Products Catalog",

        // Shepherd Tour
        tour_enabled: true,
        tour_instance: null,
        tour_steps: [
            {
                id: 'shepherd-step-one',
                title: 'Catalog Filters',
                text: 'Use filters to narrow down your search, make things easier to find the right copier for you.',
                classes: 'shepherd-step-one',
                attachTo: { element: '.v-navigation-drawer__content', on: 'right' },
                buttons: [
                    { action() { }, text: 'Dismiss', classes: 'shepherd-button' },
                    { action() { return this.next() }, text: 'Next', classes: 'shepherd-button' },
                ],
                scrollTo: true,
                useModalOverlay: true
            },
            {
                id: 'shepherd-step-two',
                title: 'Product Details',
                text: 'Click "Get a Quote" to add accessories to your printer.',
                classes: 'shepherd-step-two',
                attachTo: {
                    element: '#first-product',
                    on: 'right'
                },
                buttons: [
                    {
                        action() { return this.back() },
                        classes: 'shepherd-button',
                        text: 'Back'
                    },
                    {
                        action() { return this.cancel() },
                        classes: 'shepherd-button',
                        text: 'Okay'
                    }
                ],
                scrollTo: true,
                useModalOverlay: true
            },
            {
                id: 'shepherd-step-three',
                title: 'Configure',
                text: 'Configure your printer, add/remove accessories based on what you need.',
                classes: 'shepherd-step-three',
                attachTo: {
                    element: '#accessory-addons',
                    on: 'top'
                },
                buttons: [
                    { action() { }, text: 'Dismiss', classes: 'shepherd-button' },
                    { action() { return this.next() }, text: 'Next', classes: 'shepherd-button' }
                ],
                useModalOverlay: true
            },
            {
                id: 'shepherd-step-four',
                title: 'Add to Quote',
                text: "When you're done, click 'Add to Quote' to add the copier to your Quote",
                classes: 'shepherd-step-four',
                attachTo: {
                    element: '#add-to-quote-btn',
                    on: 'left'
                },
                buttons: [
                    {
                        action() { return this.cancel() },
                        text: 'Okay',
                        classes: 'shepherd-button',
                    }
                ],
                useModalOverlay: true
            },
            {
                id: 'shepherd-step-five',
                title: 'Fill out the form',
                text: "Please provide your details so we can get in touch with you promptly.",
                classes: 'shepherd-step-five',
                attachTo: {
                    element: '#quote-request-form',
                    on: 'bottom'
                },
                buttons: [
                    {
                        action() { return this.next() },
                        text: 'Next',
                        classes: 'shepherd-button',
                    }
                ],
                useModalOverlay: true
            },
            {
                id: 'shepherd-step-six',
                title: 'Request a Quote',
                text: "Finally, click 'Request a Quote' to submit your request.",
                classes: 'shepherd-step-six',
                attachTo: {
                    element: '#request-a-quote-btn',
                    on: 'left'
                },
                buttons: [{
                    action() { },
                    text: 'Done',
                    classes: 'shepherd-button',
                }],
                useModalOverlay: true
            },
        ],
        tour_steps_done: {
            step_one: false,
            step_two: false,
            step_three: false,
            step_four: false,
            step_five: false,
        },

        // Mobile viewport detection
        is_mobile: false,

        // AI Assistant
        assistant: {
            chats: [],
            prompt: "",
            config: null,
            theme: "light",
            visible: true,
        }
    },

    // Actions will call mutation
    actions: {
        /** Catalog Actions */
        FetchCatalogData({ commit, state }) {
            commit('SET_ANY_STATE', { target: 'is_fetching', value: true });
            Server.get('/quotetool/fetch-data').then(res => {
                if (res.data.result) {
                    let select_products = [];
                    let settings = window.catalog_config;
                    res.data.products.forEach(prod => {
                        // If product's manufacturer is allowed, then add to select_products
                        if (settings.manufacturers_allowed.includes(prod.manufacturer_id)) {

                            // If product is not in the exclusions list, then add to select_products
                            if (!settings.product_exclusions.includes(prod.id)) {
                                select_products.push(prod);
                            }
                        }
                    });

                    // Store the printers quoted by customers
                    // to be used for labelling the products "best seller"
                    const best_sellers_map = {};
                    res.data.quotes.forEach(quote => {
                        const quote_data = JSON.parse(quote.quote_info);
                        quote_data.cart_items.forEach(cart => {
                            const id = cart.product.id;
                            const name = cart.product.name || '-';
                            if (!id) return;
                            if (best_sellers_map[id]) {
                                best_sellers_map[id].count++;
                            } else {
                                best_sellers_map[id] = {id,name,count:1};
                            }
                        });
                    });
                    const best_sellers = Object.values(best_sellers_map);

                    commit('SET_ANY_STATE', { target: 'best_sellers',          value: best_sellers });
                    commit('SET_ANY_STATE', { target: 'manufacturers_allowed', value: settings.manufacturers_allowed });
                    commit('SET_ANY_STATE', { target: 'catalog_page_title',    value: settings.catalog_page_title });
                    commit('SET_ANY_STATE', { target: 'products',              value: select_products });
                    commit('SET_ANY_STATE', { target: 'manufacturers',         value: res.data.manufacturers });
                    commit('SET_ANY_STATE', { target: 'standard_function',     value: res.data.standard_function });

                    commit('SET_ANY_STATE', { target: 'categories',            value: res.data.categories });
                    commit('SET_ANY_STATE', { target: 'accessories',           value: res.data.accessories });
                    commit('SET_ANY_STATE', { target: 'lease_terms',           value: res.data.lease_terms });
                    commit('SET_ANY_STATE', { target: 'product_pricing',       value: res.data.product_pricing });
                    commit('SET_ANY_STATE', { target: 'product_reviews',       value: res.data.product_reviews });

                    // Some dataset overrides
                    const new_datasets = [];
                    res.data.datasets.forEach(dataset => {
                        new_datasets.push({...dataset,selected: dataset.default_attached == 1 ? true : false});
                    });
                    state.datasets = new_datasets;
                    commit('SET_ANY_STATE', { target: 'datasets',value: new_datasets });

                    // Set some filter vars
                    const filters = {...state.filters}
                    filters.page_limit = +settings.items_per_page;
                    commit('SET_ANY_STATE', { target: 'filters', value: filters });
                }
                else {
                    commit('SET_ANY_STATE', {
                        target: 'snackbar',
                        value: {
                            visible: true,
                            content: res.data.errors,
                            type: 'error',
                            icon: 'mdi-alert-circle-outline'
                        }
                    });
                }
            }).finally(() => {
                commit('SET_ANY_STATE', { target: 'is_fetching', value: false });
            });
        },
        SetFilters({ commit }, params) {
            commit('SET_ANY_STATE', {
                target: 'filters',
                value: params
            });
        },
        SetFormStep({ commit }, action) {
            commit('SET_FORM_STEP', action);
        },
        SetCartItems({ commit }, items) {
            commit('SET_CART_ITEMS', items);
        },
        SetSelectedCopier({ commit }, copier) {
            commit('SET_SELECTED_COPIER', copier);
        },

        /** Shared Actions */
        SetSnackbar({ commit }, params) {
            commit('SET_ANY_STATE', {
                target: 'snackbar',
                value: params
            });
        },
        SetDialog({ commit }, params) {
            commit('SET_ANY_STATE', {
                target: 'dialog',
                value: params
            });
        },
        SetError({ commit }, message) {
            commit('SET_ANY_STATE', {
                target: 'snackbar',
                value: {
                    visible: true,
                    content: message,
                    type: 'error',
                    icon: 'mdi-alert-circle-outline'
                }
            });
        },
        SetSuccess({ commit }, message) {
            commit('SET_ANY_STATE', {
                target: 'snackbar',
                value: {
                    visible: true,
                    content: message,
                    type: 'success',
                    icon: 'mdi-check-all'
                }
            })
        },
        SetAnyState({ commit }, params) {
            commit('SET_ANY_STATE', params)
        },

        /* AI Assistant */
        ToggleChatUI({commit, state}) {
            console.log("Chat UI is toggled: ", !state.visible)
            const assistantCopy = {...state.assistant};
            assistantCopy.visible = !state.assistant.visible;
            commit('SET_ANY_STATE', {
                target: "assistant",
                value: assistantCopy,
            });
        },
        AddChat({commit, state}, chat) {
            const assistantCopy = {...state.assistant};
            assistantCopy.chats.push(chat)
            commit('SET_ANY_STATE', {
                target: "assistant",
                value: assistantCopy,
            });
        },
    },

    // Mutations saves it to State
    mutations: {
        // Quote Tool Mutations
        SET_FORM_STEP(state, action) {
            if (typeof action == "number") {
                state.form_step = action;
            } else {
                if (action === 'prev') {
                    if (state.form_step > 1) {
                        state.form_step--;
                    }
                } else {
                    state.form_step++;
                }
            }
        },
        SET_CART_ITEMS(state, items) {
            state.cart_items = items;
        },
        SET_SELECTED_COPIER(state, copier) {
            state.selected_copier = copier;
        },
        SET_ANY_STATE(state, { target, value }) {
            state[target] = value;
        },
        SET_SHEPHERED_CANCELATION(state) {
            // First button when they want to dismiss
            state.tour_steps[0].buttons[0].action = () => {
                state.tour_enabled = false;
                return state.tour_instance.cancel();
            }

            // Dismiss inside the Configurator
            state.tour_steps[2].buttons[0].action = () => {
                state.tour_enabled = false;
                return state.tour_instance.cancel();
            }

            // Last tour button when they reach QuoteForm.vue
            state.tour_steps[5].buttons[0].action = () => {
                state.tour_enabled = false;
                return state.tour_instance.cancel();
            }
        },
        CLEAR_QUOTE_VALUES(state) {
            state.form.copier = {
                lease_term: 36,
                copier_type: null,
                copier_quantity: 0,
                prints_per_month: 0,

                // Mail machine
                num_mails_per_month: 0,
                owns_mailing_machine: null,

                // Plotter/wide-format
                how_many_rolls: null,
                need_a_scanner: null,
                type_of_printing: [],
                printing_dims: null,
            }
            state.form.phone = {
                phone_sys_reason: null,
                phone_quantity: null,
            }
        }
    },

    // Persistent state
    plugins: [createPersistedState({
        paths: [
            'form',
            'filters',
            'assistant',
            'cart_items',
            'configurator',
            'color_scheme',
            'tour_enabled',
            'selected_copier'
        ]
    })]
});
