<template>
    <Modal :color="ConfigModalsColor" title="Shopping Cart" v-if="cart_modal" :visible="true" @close="SetState('cart_modal',false)" max_width="960">
        <v-card-text>
            <!--Desktop View-->
            <v-data-table v-if="!is_mobile" :loading="loading" :items="cart_items" :headers="headers_desktop">
                <template v-slot:item="{item,index}">
                    <tr>
                        <td>
                            <img @click="ViewProduct(item.product)" class="table-img" :src="ProductImage(item.product)" width="50" alt="product img">
                            <div @click="ViewProduct(item.product)" class="table-txt is-desktop-view">{{ item.product.name }}</div>
                        </td>
                        <td>
                            <div class="d-inline-flex">
                                <v-btn @click="item.quantity--" variant="elevated" icon="mdi-minus" size="small" :disabled="item.quantity<=1"/>
                                <v-btn variant="text"><b>{{ item.quantity }}</b></v-btn>
                                <v-btn @click="item.quantity++" variant="elevated" icon="mdi-plus" size="small"/>
                            </div>
                        </td>
                        <td>
                            <ul>
                                <li v-for="ds in item.datasets.filter(x=>x.selected)">
                                    {{ Accessory(ds).name }}
                                </li>
                                <li v-if="item.datasets.filter(x=>x.selected).length<1">None</li>
                            </ul>
                        </td>
                        <td>
                            <v-btn rounded="pill" @click="RemoveItem(index)" prepend-icon="mdi-trash-can" color="error" size="small"
                                   variant="tonal">Delete
                            </v-btn>
                        </td>
                    </tr>
                </template>
            </v-data-table>

            <!--Mobile View-->
            <v-data-table v-if="is_mobile" :loading="loading" :items="cart_items" :headers="headers_mobile">
                <template v-slot:item="{item,index}">
                    <tr>
                        <td>
                            <div @click="ViewProduct(item.product)" class="table-txt">{{ item.product.name }}</div>
                        </td>
                        <td>
                            <v-btn @click="RemoveItem(index)" icon="mdi-trash-can" color="error" size="small" variant="tonal"/>
                        </td>
                    </tr>
                </template>
            </v-data-table>
        </v-card-text>
        <template #footer>
            <v-spacer/>
            <CheckoutBtn/>
            <v-spacer/>
        </template>
    </Modal>
</template>

<script>
import Modal from "@/components/misc/Modal.vue";
import {CommonMixins} from "@/mixins/CommonMixins";
import {ProductMixins} from "@/mixins/ProductMixins";
import {mapState,mapActions,mapMutations} from "vuex";
import CheckoutBtn from "@/components/catalog/CheckoutBtn.vue";

export default {
    mixins: [CommonMixins, ProductMixins],
    data: () => ({
        loading: false,
        headers_desktop: [
            {title: 'Copier'},
            {title: 'Qty.'},
            {title: 'Accessories'},
            {title: 'Manage'},
        ],
        headers_mobile: [
            {title: 'Copier'},
            {title: 'Manage'},
        ],

        // If being used as a part picker
        picked_items: []
    }),
    components: {CheckoutBtn, Modal},
    methods: {
        ...mapActions(['SetFormStep', 'SetCartItems']),
        ...mapMutations(['CLEAR_QUOTE_VALUES']),
        Accessory(dataset) {
            return this.accessories.find(a => a.id === dataset.accessory_id);
        },
        RemoveItem(index) {
            const items = this.cart_items;
            items.splice(index, 1);
            if (items.length < 1) {
                this.CLEAR_QUOTE_VALUES();
            }
            this.SetCartItems(items);
        },
        ViewProduct(product) {
            this.SetState('view_product',product);
        }
    },
    computed: {
        ...mapState([
            'form',
            'is_mobile',
            'cart_items',
            'cart_modal',
            'accessories',
        ])
    },
}
</script>
<style scoped>
.table-img {
    position: absolute;
    margin-top: -14px;
}

.table-txt {
    color: #164185;
    cursor: pointer;
    &:hover {
        color: #095ee3;
    }
    &.is-desktop-view {
        margin-left: 65px;
    }
}
</style>
