<template>
  <div class="filter-item">
    <div class="filter-header">
      <div class="filter-title">{{ title }}</div>
      <div v-if="$slots.default && hideButton!=true">
        <v-btn @click="display = false" size="small" style="font-size:18px" icon="mdi-chevron-up" text elevation="0"
          v-if="display" />
        <v-btn @click="display = true" size="small" style="font-size:18px" icon="mdi-chevron-down" text elevation="0"
          v-else />
      </div>
    </div>
    <div v-if="display">
      <slot/>
    </div>
  </div>
</template>

<script>
export default {
  props: ['title', 'hideButton'],
  data: () => ({
    display: true,
  })
};
</script>
<style scoped lang="scss">
.filter-item {
  margin: 20px 0;
  .filter-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .filter-title {
      font-weight: 500;
      font-size: 20px;
      font-family: Outfit;
    }
  }
}
</style>