<template>
    <div class="animate-moveFromBottom animate-delay180">
        <v-chip @click="$emit('start',text)" v-for="text in ArrayPerLine" size="small" class="ma-1 mb-2">
            <span style="margin-top:1px">{{text}}</span>
        </v-chip>
    </div>
</template>

<script>
import {mapState} from "vuex";

export default {
    computed: {
        ...mapState(['assistant']),
        ArrayPerLine() {
            if (this.assistant.config.starters.length > 0) {
                return this.assistant.config.starters.split('\n')
            } else {
                return []
            }
        }
    }
}
</script>
