<template>
    <span class="footer-buttons">
        <v-btn
            @click="SetState('addtocart_trigger',addtocart_trigger+1)"
            v-if="!config_drawer && configurator"
            :size="ResizeButtonOnSmallScreen"
            prepend-icon="mdi-plus-circle"
            :width="is_mobile?'100%':''"
            class="mt-2 mb-2 mr-2"
            :color="ConfigElementsColor"
            text="Add to Quote"
            rounded="pill">
        </v-btn>
        <v-btn
            @click="SetState('cart_modal',false)"
            :size="ResizeButtonOnSmallScreen"
            v-if="cart_modal && !is_mobile"
            :width="is_mobile?'100%':''"
            prepend-icon="mdi-plus"
            class="mt-2 mb-2 mr-2"
            :color="ConfigElementsColor"
            text="Add more..."
            rounded="pill"
            width="175">
        </v-btn>
        <v-btn
            :disabled="cart_items.length===0"
            :size="ResizeButtonOnSmallScreen"
            append-icon="mdi-chevron-right"
            :width="is_mobile?'100%':''"
            :color="ConfigElementsColor"
            @click="OpenFormModal"
            text="Request Quote"
            class="mt-2 mb-2"
            rounded="pill"
            width="175">
        </v-btn>
    </span>
</template>

<script>
import { mapState } from "vuex"
import { CommonMixins } from "@/mixins/CommonMixins";
export default {
    mixins: [CommonMixins],
    computed: {
        ...mapState([
            'is_mobile',
            'cart_items',
            'cart_modal',
            'configurator',
            'config_drawer'
        ]),
        ResizeButtonOnSmallScreen(){
            return this.is_mobile?'small':'default'
        }
    },
    methods: {
        OpenFormModal() {
            this.FormSelector();
            this.SetState('form_modal',true);
        }
    }
}
</script>
