<script>
import {mapActions} from "vuex";

export default {
    name: "Alerts",
    methods: {
        ...mapActions(['SetSnackbar', 'SetDialog']),
    },
}
</script>

<template>
    <div>
        <v-snackbar v-model="$store.state.snackbar.visible" :color="$store.state.snackbar.type">
            <template v-if="$store.state.snackbar.icon">
                <v-icon class="mr-2">{{ $store.state.snackbar.icon }}</v-icon>
            </template>
            <span v-html="$store.state.snackbar.content"></span>
            <template v-slot:action="{ attrs }">
                <v-btn color="blue" text v-bind="attrs" @click="SetSnackbar({visible:false,content:''})">Close</v-btn>
            </template>
        </v-snackbar>

        <v-dialog v-model="$store.state.dialog.visible" width="600">
            <v-card>
                <v-card-title>{{ $store.state.dialog.title }}</v-card-title>
                <v-card-text>{{ $store.state.dialog.content }}</v-card-text>
                <v-card-actions>
                    <v-btn color="darken-1" text @click="SetDialog({visible:false,title:'',content:''})" v-text="'Close'"/>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>
