<template>
    <template v-if="assistant.visible">
        <v-btn @click="NewSession" variant="flat" v-if="assistant.chats.length" size="small" class="mr-3">New Session</v-btn>
        <v-btn @click="ChangeTheme('light')" title="Click for Light theme" v-if="assistant.theme=='dark'" size="small" icon="mdi-brightness-4" elevation="0"/>
        <v-btn @click="ChangeTheme('dark')" title="Click for Dark theme" v-else size="small" icon="mdi-white-balance-sunny" elevation="0"/>
    </template>
    <v-btn @click="ToggleChatUI" v-if="assistant.visible" size="small" icon="mdi-close" elevation="0"/>
</template>

<script>
import {mapActions, mapState} from "vuex";
export default {
    computed: {
        ...mapState(['assistant'])
    },
    methods: {
        ...mapActions(['SetAnyState','ToggleChatUI']),
        NewSession() {
            if (confirm("Start a new session?")) {
                const assistant = {...this.assistant};
                assistant.chats = [];
                this.SetAnyState({target:'assistant',value:assistant});
            }
        },
        ChangeTheme(theme) {
            const assistant = {...this.assistant};
            assistant.theme = theme;
            this.SetAnyState({target:'assistant',value:assistant});
        }
    }
}
</script>
