<template>
    <v-card class="product-card-wrapper border animate-fade-in global-radius" elevation="0">
        <v-card-text class="pa-3">

            <!--Best seller pill-->
            <v-chip class="best-seller" v-if="IsBestSeller" color="#209E61" variant="elevated" elevation="0" size="x-small">Best Seller</v-chip>

            <!--Product Image-->
            <v-img :lazy-src="ProductImage(product)" class="global-radius bg-grey-lighten-4 product-image" :src="ProductImage(product)"/>

            <!--Product Title-->
            <div class="title-wrap mt-2">
                <a :href="ConfigSinglePageUrl+'/'+product.slug" class="product-title-link outfit-font" :class="{'clip-text-1-lines':!is_mobile}">{{product.name}}</a>
            </div>

            <!--Product Excerpt-->
            <div class="excerpt-wrap mt-2">
                <span class="product-excerpt outfit-font clip-text-1-lines">
                    {{product.description.replace(/<[^>]*>?/gm,'')}}
                </span>
            </div>

            <!--Product Price-->
            <div class="product-price outfit-font mt-2" v-if="ConfigShowPricing">
                <span class="starts-at">Starts at</span>
                <span class="the-price">${{product.price}}</span>
            </div>

            <!--Ratings-->
            <div class="ratings-box mt-2">
                <div class="the-ratings">
                    <v-icon icon="mdi-star" :color="ProductRatingAverage>0.0?'#FCA120':'#dadada'" class="mr-1 mb-1"/>
                    <span><span v-if="ProductReviews.length>0">{{ProductRatingAverage}}</span> ({{ProductReviews.length}}) Ratings</span>
                </div>
                <div class="free-delivery">
                    <v-icon icon="mdi-truck-fast-outline" class="mr-1"/>
                    <span>Free Delivery</span>
                </div>
            </div>
        </v-card-text>
        <v-footer style="padding-bottom: 15px;">
            <v-btn rounded="pill" size="large" :color="ConfigElementsColor" variant="tonal" class="flex-grow-1 mr-3" @click="Configure">Get a Quote</v-btn>
            <v-btn rounded="pill" size="small" :color="ConfigElementsColor" variant="tonal" icon="mdi-alert-circle-outline" :href="product.slug" target="_blank" style="font-size:14px"></v-btn>
        </v-footer>
    </v-card>
</template>

<script>
import {mapActions,mapState} from "vuex";
import {CommonMixins} from "@/mixins/CommonMixins";

export default {
    props: ['product'],
    mixins: [CommonMixins],
    methods: {
        ...mapActions(['SetSelectedCopier']),
        Configure() {
            this.SetSelectedCopier(this.product);
            this.SetState('configurator',true);
        },
        ViewProduct($event) {
            if ($event.target.href === 'javascript:;') {
                $event.preventDefault();
                this.SetState('view_product',this.product);
            }
        },
    },
    computed: {
        ...mapState([
            'selected_copier',
            'product_reviews',
            'best_sellers',
            'is_fetching',
            'is_mobile',
            'settings',
            'datasets',
        ]),
        Accessories() {
            return this.datasets.filter(ds => ds.product_id == this.product.id);
        },
        Excerpt() {
            return this.product.description.length > 60
                ? this.product.description.substring(0, 60) + '...'
                : this.product.description;
        },
        IsBestSeller() {
            return this.best_sellers.filter((prod) => prod.id == this.product.id).length>0;
        },
        HasNoDataset() {
            return this.Accessories.length<1 && !this.is_fetching && this.datasets.length > 0
        },
        ProductReviews() {
            return this.product_reviews.filter(pr => pr.product_id === this.product.id);
        },
        ProductRatingAverage() {
            const reviews = this.ProductReviews.map(pr => parseFloat(pr.rating));
            if (reviews.length === 0) return 0.0;
            const sum = reviews.reduce((acc, val) => acc + val, 0);
            return (sum / reviews.length).toFixed(1);
        },
    }
}
</script>
<style scoped>
.product-card-wrapper {
    transition: .3s;
    &:hover {
        transform: scale(1.05);
        box-shadow: 0 3px 15px rgba(139, 173, 225, 0.55) !important
    }
}
.title-wrap {
    word-break: break-word;
    font-size: 15px;
    font-weight: 500;
}
.product-image {
    width: auto;
    height: 180px;
    display: block;
    margin-top: 0 !important;
}
.prod-img-wrapper {
    min-height: 250px;
    display: flex;
    align-items: center;
}
.product-title-link {
    text-decoration: none;
    cursor: pointer;
    color: #000000;
    font-weight: 500;
    font-size: 16px;
    &:hover {
        color: #0d47a1;
        text-decoration: underline;
    }
}
.best-seller {
    top: 20px;
    left:20px;
    z-index: 3;
    position: absolute;
}
.excerpt-wrap {
    font-size:12px;
    .product-excerpt {
        height: 50px;
    }
}
.model-rating {
    display: flex;
    justify-content: space-between;
    align-items: center;

    & .model-text {
        font-size: 12px !important;
    }
    .rating-stars {
        font-size: 16px;
        text-align: right;
        color:#ff9900;
        display: inline-flex; /* or flex */
        white-space: nowrap;
        align-items: center;
    }
}
.product-price {
    .starts-at {
        font-size: 14px;
        font-weight: 400;
        margin-right: 5px;
    }
    .the-price {
        font-size: 24px;
        font-weight: 500;
    }
}
.ratings-box {
    display: flex;
    align-items: center;
    font-family: Outfit, Roboto, sans-serif;
    .the-ratings {
        font-weight: 400;
        font-size: 16px;
    }
    .free-delivery {
        font-weight: 500;
        font-size: 14px;
        margin-left: 20px;
        position: relative;
        top: -2px
    }
}
</style>
