<template>
    <Modal :color="ConfigModalsColor" :title="view_product.name" max_width="800" v-if="view_product" footer_color="#f1f1f1" :visible="true" @close="CloseModal">
        <v-card-text>
            <v-row>
                <v-col cols="12" md="6" lg="6" sm="12" xs="12">
                    <img :src="`${ProductImage(view_product)}`" alt="" style="max-width: 100%;">
                </v-col>
                <v-col cols="12" md="6" lg="6" sm="12" xs="12" class="d-flex align-center">
                    <div>
                        <table class="view-prod-table">
                            <tr v-if="NotPhone">
                                <td>Paper Size</td>
                                <td>{{view_product.copier_type}}</td>
                            </tr>
                            <tr>
                                <td>Manufacturer</td>
                                <td>{{Brand(view_product.manufacturer_id).name}}</td>
                            </tr>
                            <tr>
                                <td>Standard Function</td>
                                <td>{{view_product.standard_function}}</td>
                            </tr>
                            <tr>
                                <td>Model Number</td>
                                <td>{{view_product.model_number}}</td>
                            </tr>
                            <tr v-if="NotPhone">
                                <td>Color/Monochrome</td>
                                <td>{{view_product.copier_color}}</td>
                            </tr>
                            <tr v-if="view_product.speed_color && NotPhone && view_product.standard_function != 'Wide-Format'">
                                <td>Speed (Color)</td>
                                <td>{{view_product.speed_color}} ppm</td>
                            </tr>
                            <tr v-if="view_product.speed_black && NotPhone && view_product.standard_function != 'Wide-Format'">
                                <td>Speed (Black)</td>
                                <td>{{view_product.speed_black}} ppm</td>
                            </tr>
                        </table>
                    </div>
                </v-col>
            </v-row>
            <div class="product-description pa-3 mt-5 rounded" v-html="view_product.description"></div>
        </v-card-text>
        <template v-slot:footer>
            <v-spacer/>
            <v-btn class="mt-1 mb-1 ml-1" @click="Configure" :color="ConfigElementsColor" prepend-icon="mdi-printer">Configure</v-btn>
            <v-spacer/>
        </template>
    </Modal>
</template>

<script>
import {mapActions, mapState} from "vuex";
import Modal from "@/components/misc/Modal.vue";
import {ProductMixins} from "@/mixins/ProductMixins";
import {CommonMixins} from "@/mixins/CommonMixins";
export default {
    mixins: [ProductMixins,CommonMixins],
    components: {Modal},
    computed: {
        ...mapState(['view_product','manufacturers']),
        NotPhone() {
            return this.view_product.standard_function != 'Phone';
        }
    },
    methods: {
        ...mapActions(['SetSelectedCopier']),
        Brand(id) {
            return this.manufacturers.find(b => b.id == id);
        },
        CloseModal() {
            this.SetState('view_product',null);
        },
        Configure() {
            this.SetSelectedCopier(this.view_product);
            this.SetState('configurator',true);
            this.SetState('cart_modal',false);
            this.CloseModal();
        },
    },
}
</script>
<style >
.view-prod-table {
    & th,
    & td {
        font-family: Quicksand, Roboto, sans-serif;
    }
    & {
        width: 100%;
        border-collapse: collapse;
    }
    & tr {
        & td:first-child {
            padding-right: 20px;
        }
        & td:last-child {
            font-weight: 600;
        }
    }
    & tr td {
        padding: 5px 10px;
        border: 1px solid #d1d1d1;
        border-right: none;
        border-left: none;
    }
}
.product-description {
    font-family: Quicksand, Roboto, sans-serif;
    & ul li p {
        display: inline;
    }
}
.product-description ul {
    margin-left: 18px;
}
</style>
