<template>
    <div :class="`chat from-${chat.role} animate-fadeIn`">
        <div class="chat-date">
            <v-icon v-if="chat.role=='ai'" icon="mdi-robot-outline"/>
            {{FormatDate}}
        </div>
        <div class="chat-content" v-html="chat.message"></div>
    </div>
</template>

<script>
import moment from "moment";
export default {
    props: ['chat'],
    computed: {
        FormatDate() {
            return moment(this.chat.timestamp).format("MMMM D, YYYY h:mm A");
        }
    }
}
</script>
<style lang="scss">
.chat {
    .chat-date {
        font-size: 12px;
        margin-bottom: 10px;
        .v-icon {
            position: relative;
            top: -3px;
            margin-right: 3px
        }
    }
    .chat-content ul {
        padding-left: 15px
    }
}
</style>
