<template>
    <div ref="draggable" class="stacked-images">
        <img id="base-image" :src="BaseImage(product)" v-if="has_base_img && have_accessories">
        <img id="feat-image" :src="ProductImage(product)" v-else>
        <template v-for="(dataset,key) in attachments" :key="key">
            <img class="img-stack animate-fade-in" :style="`z-index:${key};${CustomAccessoryCss(dataset)}`" v-if="dataset.filename" :src="DatasetImage(dataset)">
        </template>
    </div>
</template>

<script>
import {ProductMixins} from "@/mixins/ProductMixins";
import {AccessoryMixins} from "@/mixins/AccessoryMixins";
import interact from "interactjs";
import {mapActions, mapState} from "vuex";

export default {
    props: ['attachments','product'],
    mixins: [ProductMixins,AccessoryMixins],
    computed: {
        have_accessories() {
            return this.attachments.length > 0;
        },
        has_base_img() {
            if (this.product.base_image == null) return false;
            return this.product.base_image.length > 0;
        },
        ...mapState(['accessories','tour_instance','tour_enabled','tour_steps_done'])
    },
    methods: {
        ...mapActions(['SetAnyState']),
        CustomAccessoryCss(dataset) {
            let csstxt = "";
            const rules = JSON.parse(dataset.rules);
            rules.forEach(rule => {
                if (rule.type == 'custom_css') {
                    // check if all items in rule.items are selected
                    let is_condition_met = 0;
                    rule.items.forEach(rule_item_id => {
                        let find_item = this.attachments.find(ex => ex.id==rule_item_id && ex.selected);
                        if (typeof find_item != 'undefined') {
                            is_condition_met++;
                            console.log("is_condition_met")
                        }
                    });
                    if (is_condition_met == rule.items.length && rule.items.length > 0) {
                        csstxt = rule.css;
                    }
                }
            });
            return csstxt;
        },
    },
    mounted() {
        interact(this.$refs.draggable).draggable({
            listeners: {
                move(event) {
                    const target = event.target;
                    const x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx;
                    const y = (parseFloat(target.getAttribute('data-y')) || 0) + event.dy;
                    target.style.transform = `translate(${x}px, ${y}px)`;
                    target.style.transition = '0s';
                    target.setAttribute('data-x', x);
                    target.setAttribute('data-y', y);
                },
            },
        });
    },
    unmounted() {
        this.tour_instance.cancel();
        const tour_steps_done = {...this.tour_steps_done};
        tour_steps_done.step_three = true;
        this.SetAnyState({
            target: 'tour_steps_done',
            value: tour_steps_done,
        });
    },
    watch: {
        accessories() {
            // Start tour in Configurator.vue when accessories are loaded
            this.tour_instance.start();
            this.tour_instance.show('shepherd-step-three');
        }
    }
}
</script>
<style scoped>
/* Product Builder */
.stacked-images {
    width: fit-content;
    height: fit-content;
    margin: auto;
    position: relative;
    user-select: none;
    -webkit-user-drag: none;
    user-drag: none;
    & img.img-stack {
        position: absolute;
        object-fit: contain;
        top: 0;
        left: 0;
    }
    & #feat-image,
    & #base-image,
    & img {
        width: 100%;
        height: 100%;
    }
}
</style>
