<template>
    <ShopBanner v-if="ConfigShowShopBanner"/>
    <v-app id="layout-app">
        <v-app-bar density="comfortable" :color="ConfigTopbarColor" :elevation="0" class="border-b">
            <v-app-bar-nav-icon variant="text" @click.stop="drawer=!drawer"/>
            <v-spacer/>
            <HelperIcon/>
            <CopierCart :floated="false"/>
            <Branding/>
         </v-app-bar>
        <v-navigation-drawer width="300" v-model="drawer" location="left">
            <v-card elevation="0" class="pa-5">
                <v-card-text class="pa-3">
                    <v-text-field
                        prepend-inner-icon="mdi-magnify"
                        v-model="SearchBox"
                        class="search-box"
                        variant="outlined"
                        bg-color="#F8F8F8"
                        density="compact"
                        label="Search"
                        type="search"
                        rounded="lg">
                    </v-text-field>

                    <FilterItem title="Filters" :hide-button="true">
                        <FilterTags/>
                    </FilterItem>

                    <v-divider/>

                    <FilterItem title="Brands">
                        <CustomCheckbox :label="make.name"
                            v-for="(make) in manufacturers.filter(man => manufacturers_allowed.includes(man.id))"
                            @CheckChange="SetManufacturers($event,make.id)"
                            :is_checked="ItemExists('manufacturers',+make.id)">
                        </CustomCheckbox>
                    </FilterItem>

                    <v-divider/>

                    <FilterItem title="Device">
                        <CustomCheckbox
                            :label="item.standard_function"
                            v-for="item in standard_function"
                            @CheckChange="SetAnyFilter($event,'device_types',item.standard_function)"
                            :is_checked="ItemExists('device_types',item.standard_function)">
                        </CustomCheckbox>
                    </FilterItem>

                    <v-divider/>

                    <FilterItem title="Printer Type">
                        <CustomCheckbox label="Color" @CheckChange="SetAnyFilter($event,'copier_colors','Color')" :is_checked="ItemExists('copier_colors','Color')"/>
                        <CustomCheckbox label="Mono"  @CheckChange="SetAnyFilter($event,'copier_colors','B&W')"   :is_checked="ItemExists('copier_colors','B&W')"/>
                    </FilterItem>

                    <v-divider/>

                    <FilterItem title="Speed">
                        <v-row>
                            <v-col cols="12" lg="6" md="6" sm="12">
                                <v-text-field @input="SetSpeedMin" :model-value="filters.speed_minimum" rounded="lg" type="number" variant="outlined" density="compact" label="Min" placeholder="min"/>
                            </v-col>
                            <v-col cols="12" lg="6" md="6" sm="12">
                                <v-text-field @input="SetSpeedMax" :model-value="filters.speed_maximum" rounded="lg" type="number" variant="outlined" density="compact" label="Max" placeholder="max"/>
                            </v-col>
                        </v-row>
                    </FilterItem>

                    <v-divider/>

                    <FilterItem title="Paper Size">
                        <CustomCheckbox label="Legal/Letter/A4" @CheckChange="SetAnyFilter($event,'paper_sizes','A4')" :is_checked="ItemExists('paper_sizes','A4')"/>
                        <CustomCheckbox label="Ledger/Tabloid/A3" @CheckChange="SetAnyFilter($event,'paper_sizes','A3')" :is_checked="ItemExists('paper_sizes','A3')"/>
                        <CustomCheckbox label="Wide-Format/Large Format" @CheckChange="SetAnyFilter($event,'paper_sizes','WF')" :is_checked="ItemExists('paper_sizes','WF')"/>
                    </FilterItem>

                    <v-divider/>

                    <FilterItem title="Prices">
                        <CustomCheckbox
                            :label="price_item.label"
                            :is_checked="PriceFilterExists(price_item)"
                            v-for="price_item in filters.price_options"
                            @CheckChange="SetPriceFilter($event,price_item)">
                        </CustomCheckbox>
                    </FilterItem>
                </v-card-text>
            </v-card>
        </v-navigation-drawer>
        <v-main>
            <v-card-text class="pa-1 pa-sm-3 pa-md-5" style="background:#f1f1f1">
                <slot name="content"/>
            </v-card-text>
            <v-footer v-if="$slots.footer" style="background:#f1f1f1" elevation="1">
                <slot name="footer"/>
            </v-footer>
        </v-main>
    </v-app>
</template>

<script>
import {mapActions, mapState} from "vuex";
import { CommonMixins } from "@/mixins/CommonMixins";
import CopierCart from "@/components/configurator/CopierCart.vue";
import HelperIcon from "@/components/configurator/HelperIcon.vue";
import CustomCheckbox from "@/components/typography/CustomCheckbox.vue";
import FilterTags from "@/components/catalog/FilterTags.vue";
import FilterItem from "@/components/catalog/FilterItem.vue";
import Branding from "@/components/misc/Branding.vue";
import ShopBanner from "@/components/catalog/ShopBanner.vue";

export default {
    mixins: [CommonMixins],
    components: {ShopBanner, Branding, FilterTags, HelperIcon, CustomCheckbox, CopierCart, FilterItem},
    data: () => ({
        drawer: true,
        is_logged_in: false,
        profile: null,
        controls: {
            brands: false,
        }
    }),
    methods: {
        ...mapActions(['SetFilters','SetAnyState']),
        SetManufacturers(is_checked, manufacturer_id) {
            if (typeof is_checked == "object") {
                is_checked = is_checked.target.checked;
            }
            const rawFilters = { ...this.filters };
            const exists = rawFilters.manufacturers.find(m=>m==+manufacturer_id);
            if (is_checked == true) {
                if (!exists) {
                    rawFilters.manufacturers.push(+manufacturer_id);
                }
            }
            else {
                if (exists) {
                    const index = rawFilters.manufacturers.indexOf(+manufacturer_id);
                    if (index !== -1) {
                        rawFilters.manufacturers.splice(index,1);
                    }
                }
            }
            this.SetFilters(rawFilters);
            this.$forceUpdate();
        },
        SetSpeedMin(event) {
            const filters = {...this.filters};
            filters.speed_minimum = +event.target.value;
            if (event.target.value.length < 1) filters.speed_minimum = null;
            this.SetFilters(filters);
        },
        SetSpeedMax(event) {
            const filters = {...this.filters};
            filters.speed_maximum = +event.target.value;
            if (event.target.value.length < 1) filters.speed_maximum = null;
            this.SetFilters(filters);
        },
        SetAnyFilter(is_checked, target, value) {
            if (typeof is_checked == "object") {
                is_checked = is_checked.target.checked;
            }
            const rawFilters = { ...this.filters };
            const exists = rawFilters[target].find(c=>c==value);
            if (is_checked == true) {
                if (!exists) {
                    rawFilters[target].push(value);
                }
            }
            else {
                if (exists) {
                    const index = rawFilters[target].indexOf(value);
                    if (index !== -1) {
                        rawFilters[target].splice(index,1);
                    }
                }
            }
            this.SetFilters(rawFilters);
            this.$forceUpdate();
        },
        SetPriceFilter(is_checked,min_max) {
            const is_exists  = this.PriceFilterExists(min_max);
            const filtersCopy = {...this.filters}

            if (is_checked) {
                if (!is_exists) {
                    filtersCopy.price_filters.push(min_max);
                }
            } else {
                if (is_exists) {
                    const index = filtersCopy.price_filters.findIndex(
                        filter => filter.min === min_max.min && filter.max === min_max.max
                    );
                    if (index != -1) {
                        filtersCopy.price_filters.splice(index,1);
                    }
                }
            }
            this.SetFilters(filtersCopy);
        },
        PriceFilterExists(min_max) {
            return this.filters.price_filters.some(
                filter => filter.min === min_max.min && filter.max === min_max.max
            );
        },
        ItemExists(target,value) {
            return this.filters[target].includes(value);
        },
    },
    mounted() {
        // Set defaults for drawers on small screens
        const isMobile = window.innerWidth <= 768;
        if (isMobile) {
            this.drawer = false;
            this.SetAnyState({target:'config_drawer',value:false});
            this.SetAnyState({target:'is_mobile',value:isMobile});
        }
    },
    computed: {
        ...mapState([
            'products',
            'filters',
            'manufacturers',
            'standard_function',
            'manufacturers_allowed',
            'catalog_page_title',
            'settings'
        ]),
        SearchBox: {
            get() {
                return this.filters.search_input;
            },
            set(value) {
                const filters = {...this.filters}
                filters.search_input = value;
                this.SetFilters(filters);
            }
        }
    },
}
</script>
<style lang="scss">
#layout-app {
    .v-application__wrap {
        border-bottom: none !important;
        height: fit-content !important;
        margin-bottom: 0;
        min-height: 120vh;
    }
}
</style>
