<template>
    <v-footer class="text-center" style="position:relative;top:150px;">
        <v-btn v-if="copier" size="large" @click="AddToCart" color="success" prepend-icon="mdi-cart" class="mt-5">Add to cart</v-btn>
    <!--<div>
            <div class="roboto-font">
                <template v-if="copier">
                    <h3>${{ ProductPrice }} / Month</h3>
                    <p>(Tax Exclusive)</p>
                </template>
            </div>
            <v-btn v-if="copier" @click="AddToCart" color="primary" prepend-icon="mdi-cart" variant="tonal" class="mt-5">Add to cart</v-btn>
        </div>-->
    </v-footer>
</template>

<script>
import {mapState} from "vuex";

export default {
    props: ['copier','form'],
    emits: ['add-to-cart'],
    computed: {
        ...mapState(['product_pricing']),
        ProductPrice() {
            if (this.copier) {
                const price = this.product_pricing.find(item => {
                    return item.product_id == this.copier.id && item.term == this.form.lease_term;
                });
                if (typeof price !== 'undefined') {
                    return price.price;
                } else {
                    return 0;
                }
            } else {
                console.log('Oh no')
                return 0;
            }
        }
    },
    methods: {
        AddToCart() {
            this.$emit('add-to-cart')
        }
    }

}
</script>
