<template>
    <v-text-field
        type="number"
        min="0"
        max="20000"
        label="Estimated Monthly Prints"
        :model-value="form.prints_per_month.max"
        @update:modelValue="$emit('select',$event)"
        variant="outlined"
        :rules="[positiveNumberRule]"
        class="mt-5">
    </v-text-field>
</template>

<script>
export default {
    props: ['form'],
    computed: {
        positiveNumberRule() {
            return (value) => {
                if (value === '' || parseFloat(value) >= 0) {
                    return true; // Input is valid
                }
                return 'Please enter a number greater than or equal to zero';
            };
        }
    }
}
</script>
