<template>
    <div class="copier-cart" :class="{'floated-cart':floated}">
        <Tooltip :text="`(${cart_items.length}) items in your cart`">
            <div class="icon-wrapper" @click="SetAnyState({target:'cart_modal',value:true})" style="position: relative;display: inline-block;">
                <v-icon size="25">mdi-cart-outline</v-icon>
                <span class="custom-badge" :style="`background:${ConfigElementsColor}`">{{ cart_items.length }}</span>
            </div>
        </Tooltip>
    </div>
</template>
<script>
import Modal from "@/components/misc/Modal.vue";
import {mapActions, mapState} from "vuex";
import Tooltip from "@/components/typography/Tooltip.vue";
import {CommonMixins} from "@/mixins/CommonMixins";

export default {
    props: ['floated'],
    mixins: [CommonMixins],
    components: {Tooltip, Modal},
    computed: {
        ...mapState(['cart_items','cart_modal'])
    },
    methods: {
        ...mapActions(['SetFormStep','SetAnyState'])
    }
}
</script>
<style>
.copier-cart {
    margin-right: 30px;
}
.floated-cart {
    position: absolute;
    top: 15px;
    right: 15px;
}
.custom-badge {
    position: absolute;
    top: -12px;
    right: -12px;
    border-radius: 50px;
    width: 22px;
    height: 22px;
    text-align: center;
    color: #ffffff;
    font-size: 10px;
    font-weight: 600;
    padding-top: 3px
}
</style>
