<template>
    <v-dialog class="outfit-font" scrim="rgba(0, 0, 0, 0.8)" v-model="configurator" :fullscreen="config_fullscreen" persistent :width="config_fullscreen ? '100%':'90%'" :max-width="!config_fullscreen ? '1366px':'100%'" :scrim="false" transition="dialog-bottom-transition">
        <v-card :class="GlobalRadius">
            <v-layout class="overflow-y-auto">
                <v-app>
                    <v-app-bar density="comfortable" :elevation="0" :class="`${GlobalRadius} config-app-bar border-b position-fixed`">
                        <v-btn icon="mdi-close" dark @click="Close"/>
                        <v-spacer/>
                        <span v-text="selected_copier.name"/>
                        <v-spacer/>
                        <v-btn icon="mdi-menu" dark @click="ToggleDrawer"/>
                        <v-btn icon="mdi-fullscreen" dark @click="ToggleFullscreen"/>
                    </v-app-bar>
                    <v-main class="bg-white">
                        <v-card-text>
                            <slot name="main"/>
                        </v-card-text>
                    </v-main>
                    <v-navigation-drawer v-model="DrawerProxy" location="right">
                        <slot name="drawer"/>
                    </v-navigation-drawer>
                </v-app>
            </v-layout>
            <v-footer class="configurator-footer border-t pa-0 pl-3 pr-3" style="max-height: fit-content">
                <ShopLink v-if="!is_mobile"/>
                <v-spacer/>
                <CheckoutBtn/>
            </v-footer>
        </v-card>
    </v-dialog>
</template>

<script>
import {mapActions, mapState} from "vuex";
import CheckoutBtn from "@/components/catalog/CheckoutBtn.vue";
import ShopLink from "@/components/misc/ShopLink.vue";

export default {
    components: {ShopLink, CheckoutBtn},
    methods: {
        ...mapActions(['SetAnyState']),
        ToggleDrawer() {
            this.SetAnyState({
                target: 'config_drawer',
                value: !this.config_drawer
            });
        },
        ToggleFullscreen() {
            this.SetAnyState({
                target: 'config_fullscreen',
                value: !this.config_fullscreen
            });
        },
        Close() {
            this.SetAnyState({
                target: 'configurator',
                value: !this.configurator
            });
        },
        HandleEscape(event) {
            if (event.key === 'Escape') {
                this.Close();
            }
        }
    },
    mounted() {
        window.addEventListener('keydown', this.HandleEscape);
    },
    beforeUnmount() {
        window.removeEventListener('keydown', this.HandleEscape);
    },
    computed: {
        ...mapState([
            'is_mobile',
            'configurator',
            'config_drawer',
            'selected_copier',
            'config_fullscreen'
        ]),
        DrawerProxy: {
            get() {
                return this.config_drawer;
            },
            set(val) {
                this.SetAnyState({target:'config_drawer',value:val});
            }
        },
        GlobalRadius() {
            if (this.config_fullscreen) {
                return '';
            } else {
                return 'global-radius';
            }
        }
    }
}
</script>
<style scoped>
@media(max-width: 600px) {
    .configurator-footer {
        display: block;
    }
}
.config-app-bar {
    border-bottom-right-radius: 0 !important;
    border-bottom-left-radius: 0 !important
}
</style>
